import React, { Component } from 'react';
import { Container } from "react-bootstrap";
import Card from "../components/Card";

class Cards extends Component {
    constructor(props) {
        super(props);
        this.state = {
            movies: [],
        };
    }
    componentDidMount() {
        fetch('https://api.airtable.com/v0/appeWVfjZSZCKffa9/Opvolglijst?api_key=keyVSrCrrUoAU2Sh5')
            .then((resp) => resp.json())
            .then(data => {
                //console.log(data);
                this.setState({ movies: data.records });
                //console.log(this.state.movies.filter(movie => movie.fields.Bron == "test"));
            })
            .catch(err => {
                // Error
            });
    }
    render() {
        return (
            <Container>
                <Container className="b">
                    <h5 className="for-h">Status mijn dossiers</h5>
                    <br />
                    <div className="card-columns">
                        {this.state.movies.filter(movie => movie.fields.Bron == "Malines Group").map(movie => <Card {...movie.fields} /> )}
                    </div>
                </Container>
            </Container>
        );
    }
}

export default Cards;
