import React, {useState} from "react";
import {Container, Row, Col, Form, Button} from "react-bootstrap";
import App from "../App";

function HomePage(props) {
    const [validated, setValidated] = useState(false);

    const handleSubmit = (event) => {
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        setValidated(true);
    }

    const [validated2, setValidated2] = useState(false);

    const handleSubmit2 = (event) => {
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        setValidated2(true);
    }

    function isEmpty(obj) {
        for (let prop in obj) {
            if (obj.hasOwnProperty(prop)) {
                return false;
            }
        }
        return true;
    }
    async function GetUsers(props) {
        let email_auth = document.getElementById("email-auth").value;
        let password = document.getElementById("password").value;

        if (email_auth !== "" && password !== "") {
            let filterByFormula = '&filterByFormula=AND({email}="'+email_auth+'",{password}="'+password+'")'
            let url = 'https://api.airtable.com/v0/appeWVfjZSZCKffa9/Users?api_key=keyVSrCrrUoAU2Sh5'+filterByFormula;
            let response = await fetch(url);

            if (response.ok) { // если HTTP-статус в диапазоне 200-299 получаем тело ответа
                let json = await response.json();
                let records = json.records;
                console.log(json);
                if(isEmpty(records)) {
                    alert('Wrong email or password.');
                }
                else {
                    //console.log(json.records[0].fields.email);
                    let display_name = json.records[0].fields.display_name;
                    console.log(display_name);
                    //window.location.href = "#/new";
                    // ReactDOM.render(
                    //     <React.StrictMode>
                    //         <HashRouter>
                    //             <Greeting isLoggedIn={true} />
                    //         </HashRouter>
                    //     </React.StrictMode>,
                    //     document.getElementById('root')
                    // )
                    //Greeting(props.isLoggedIn = true);
                    alert('Welcome back '+display_name+'!');
                    App({isLoggedIn: false});
                    window.location.href = "#/new_alt";
                    // const element = <App aa="login" />;
                    // ReactDOM.render(
                    //     element,
                    //     document.getElementById('root')
                    // )
                }
            }
            else {
                alert("Ошибка HTTP: " + response.status);
            }
        }
    }
    function Compare() {
        let email_reg = document.getElementById("email-reg").value;
        let email_reg_double = document.getElementById("email-reg-double").value;

        if (email_reg !== "" && email_reg_double !== "") {
            if (email_reg !== email_reg_double)
                alert("Emails is different! Check it please!");
            else {
                window.location.href = "#/new_user";
            }
        }
    }
    return (
        <Container>
            <Container className="b">
                <h5 className="for-h">Welkom bij mijn portal - meld je aan</h5>
                <br />
                <Row>
                    <Col md className="border-right">
                        <div className="outer">
                            <span className="inner">
                                Ik heb al een account bij Electro-test
                            </span>
                        </div>
                        <Form noValidate validated={validated} onSubmit={handleSubmit}>
                            <Form.Group controlId="formBasicEmail">
                                <Form.Label className="text-muted form_font-size">E-mailadres*</Form.Label>
                                <Form.Control required id="email-auth" type="email" placeholder="" />
                                {/*<Form.Text className="text-muted">*/}
                                {/*    We'll never share your email with anyone else.*/}
                                {/*</Form.Text>*/}
                            </Form.Group>
                            <Form.Group controlId="formBasicPassword">
                                <Form.Label className="text-muted form_font-size">Wachtwoord*</Form.Label>
                                <Form.Control required id="password" type="password" placeholder="" />
                            </Form.Group>
                            {/*<Form.Group controlId="formBasicCheckbox">*/}
                            {/*    <Form.Check type="checkbox" label="Check me out" />*/}
                            {/*</Form.Group>*/}
                            <Button id="login_button" variant="outline-secondary" type="submit" onClick={() => GetUsers()} size="sm" block>
                                Aanmelden
                            </Button>
                        </Form>
                    </Col>
                    <Col md>
                        <div className="outer">
                            <span className="inner">
                                Ik heb nog geen account bij Electro-test
                            </span>
                        </div>
                        <Form noValidate validated={validated2} onSubmit={handleSubmit2}>
                            <Form.Group controlId="formBasicEmail">
                                <Form.Label className="text-muted form_font-size">E-mailadres*</Form.Label>
                                <Form.Control required id="email-reg" type="email" placeholder="" />
                                {/*<Form.Text className="text-muted">*/}
                                {/*    We'll never share your email with anyone else.*/}
                                {/*</Form.Text>*/}
                            </Form.Group>
                            <Form.Group controlId="exampleForm.ControlSelect1">
                                <Form.Label className="text-muted form_font-size">Herhaal e-mailadres*</Form.Label>
                                <Form.Control required id="email-reg-double" type="email" placeholder="" />
                            </Form.Group>
                            {/*<Form.Group controlId="formBasicCheckbox">*/}
                            {/*    <Form.Check type="checkbox" label="Check me out" />*/}
                            {/*</Form.Group>*/}
                            <Button variant="outline-secondary" type="submit" onClick={() => Compare()} size="sm" block>
                            {/*<Button variant="secondary" type="submit" as={Link} to="/new_user">*/}
                                Verder
                            </Button>
                        </Form>
                    </Col>
                </Row>
            </Container>
        </Container>
    );
}

export default HomePage;
